<template>
  <div class="contaier-consumptionDetails">
    <div class="list">
      <div
        class="item"
        :class="[index%2==0?'flex56':'flex44',item.payAmounts?'activeItem':'']"
        v-for="(item,index) in details"
        :key="index"
      >
        <span>{{item.text}}:</span>
        <span v-if="item.orderTime">{{item.value|orderTime}}</span>
        <span v-else>{{item.value}}</span>
      </div>
    </div>
  </div>
</template>
<script>
import ajaxGet from "@/conf/ajaxGet";
import { mapState, mapGetters } from "vuex";
export default {
  props: {},
  data() {
    return {
      details: [],
      needPayAmounts: 0
    };
  },
  computed: {
    ...mapState({
      selectedTableInfo: state => state.mobileOrdering.selectedTableInfo,
      masterInfo: state => state.mobileOrdering.masterInfo
    })
  },
  async created() {
    this.needPayAmounts = Number.parseFloat(this.masterInfo.needPayAmounts);
    this.details = this.masterInfo.details;
    this.$emit("emitConsumptionDetails", this.needPayAmounts);
  },
  filters: {
    orderTime(val) {
      return val.replace(/-/g, "/").substring(2);
    }
  }
};
</script>
<style lang="less" scoped>
.contaier-consumptionDetails {
  background-color: #393c41;
  padding-bottom: 10px;
  .list {
    width: 100%;
    padding: 10px 15px;
    box-sizing: border-box;
    background-color: #545967;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    .item {
      font-size: 14px;
      color: #ffffff;
      text-align: left;
      letter-spacing: -0.5px;
    }
    .activeItem {
      color: #c6af6c;
    }
    .flex56 {
      width: 57%;
    }
    .flex44 {
      width: 43%;
    }
  }
}
</style>



