<template>
  <div class="container-scanCode">
    <headerUsually btnShow="true" titleText="扫码支付" url="/mobileOrdering/pay"></headerUsually>
    <consumptionDetails @emitConsumptionDetails="consumptionDetailsFun($event)"></consumptionDetails>
    <div class="container-main">
      <div class="list">
        <div class="item quantity">
          <div class="left">付款码</div>
          <div class="right">
            <input type="text" v-model="cardNumber" />
          </div>
        </div>
        <div class="item quantity">
          <div class="left">实收</div>
          <div class="right">
            <input type="text" v-model="income" />
          </div>
        </div>
        <div class="item remarks">
          <div class="left">备注</div>
          <div class="right">
            <input type="text" v-model="remarks" />
          </div>
        </div>
      </div>
    </div>
    <div class="btn" @click="okFun()">确定</div>
  </div>
</template>
<script>
import ajaxGet from "@/conf/ajaxGet";
import { Toast } from "mint-ui";
import { mapState, mapGetters } from "vuex";
import headerUsually from "@/components/common/mobileOrdering/headerUsually";
import consumptionDetails from "@/components/common/mobileOrdering/consumptionDetails";
let scan = null;
export default {
  components: {
    headerUsually,
    consumptionDetails
  },
  data() {
    return {
      income: "",
      cardNumber: "",
      remarks: "",
      payCode: "",
      needPayAmounts: 0,
      codeUrl: ""
    };
  },
  computed: {
    ...mapState({
      selectedTableInfo: state => state.mobileOrdering.selectedTableInfo
    })
  },
  async created() {
    this.getbarcode();
    this.payCode = this.$route.params.payCode;
  },
  methods: {
    getbarcode: _.debounce(
      function() {
        var A = plus.webview.getLaunchWebview();
        var that = this;
        window.barback = function(a) {
          console.log("a:", a);
          that.cardNumber = a;
        };
        A.evalJS("getcardCode()");
      },
      500,
      true
    ),
    consumptionDetailsFun(val) {
      this.needPayAmounts = val;
    },
    async okFun() {
      if (this.income == "") {
        this.msg("请输入支付金额！");
        return;
      }
      if (this.cardNumber == "") {
        this.msg("付款码不能为空！");
        return;
      }
      let incomeNum = Number(this.income);
      if (incomeNum < 0) {
        this.msg("支付金额必须为非负数！");
        return;
      }
      if (incomeNum > this.needPayAmounts) {
        this.msg("支付过多！");
        return;
      }
      let coi = "";
      if (incomeNum >= this.needPayAmounts) {
        coi = 0;
      } else if (incomeNum < this.needPayAmounts) {
        coi = 1;
      }
      let data = {
        params: {
          accnt: this.selectedTableInfo.accnt,
          payCode: this.payCode,
          coi: coi,
          charge: incomeNum,
          cardNo: this.cardNumber,
          remarks: this.remarks
        }
      };
      let res = await ajaxGet("/pos/router?method=updateCheckOut", data);
      if (res && res.resultCode == 0) {
        this.msg("支付成功！");
        if (res.data && res.data.billNo) {
          this.$router.push("/mobileOrdering/index");
        } else {
          this.$router.push("/mobileOrdering/pay");
        }
      }
    },
    msg(msg) {
      Toast({
        message: msg,
        position: "middle",
        duration: 3000
      });
    }
  },
  watch: {
    income(val) {
      //console.log("val:", val);
      this.income = this.income.replace(/[^\d.]/g, ""); //清除“数字”和“.”以外的字符
      this.income = this.income.replace(/\.{2,}/g, "."); //只保留第一个. 清除多余的
      this.income = this.income
        .replace(".", "$#$")
        .replace(/\./g, "")
        .replace("$#$", ".");
      this.income = this.income.replace(/^(\-)*(\d+)\.(\d\d).*$/, "$1$2.$3"); //只能输入两个小数
      if (this.income.indexOf(".") < 0 && this.income != "") {
        //以上已经过滤，此处控制的是如果没有小数点，首位不能为类似于 01、02的金额
        this.income = parseFloat(this.income).toString();
      }
    },
    cardNumber(val) {
      this.cardNumber = this.cardNumber.replace(/[^\d]/g, ""); //清除“数字”和“.”以外的字符
    }
  }
};
</script>

<style lang="less" scoped>
.container-scanCode {
  padding-top: 46px;
  padding-bottom: 20px;
  .container-main {
    padding: 0 14px;
    background-color: #ffffff;
    .list {
      padding-left: 18px;
      background-color: #fff;
      .item {
        height: 65px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        border-bottom: 1px solid #ddd;
        padding-right: 17px;
        .left {
          font-size: 14px;
          color: #666666;
        }
        .right {
          width: 70%;
          display: flex;
          align-items: center;
          font-size: 14px;
          color: #333333;
          input {
            text-align: right;
            outline: none;
            border: none;
            width: 100%;
          }
          .iconfont {
            color: #a8acb8;
          }
        }
      }
      .item:last-child {
        border-bottom: none;
      }
      .quantity {
        .right {
          .iconfont {
            color: #c4ad68;
            font-size: 30px;
          }
          .num {
            margin: 0 20px;
          }
        }
      }
    }
  }
  .btn {
    margin: 20px 15px 0;
    height: 40px;
    line-height: 40px;
    background: #c6af6c;
    border-radius: 5px;
    font-size: 14px;
    color: #ffffff;
  }
  .scan {
    height: 100%;
    #bcid {
      width: 100%;
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 3rem;
      text-align: center;
      color: #fff;
      background: #ccc;
    }
    footer {
      position: absolute;
      left: 0;
      bottom: 1rem;
      height: 2rem;
      line-height: 2rem;
      z-index: 2;
    }
  }
}
</style>



